import React from 'react';
import { Card, CardContent, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, CircularProgress, Select, MenuItem } from '@mui/material';

function RequestCard({ request, onUpdateStatus, onUpdateItemStatus, updatingId }) {
  const isAllCompleted = request.items.every(item => item.status.trim() === '');

  const handleAllComplete = () => {
    onUpdateStatus(request.id, '');
  };

  const handleItemStatusChange = (e, item) => {
    const newStatus = e.target.value;
    onUpdateItemStatus({
      orderId: request.id,
      itemName: item.subcategory,
      thickness: item.thickness,
      size: item.size,
      newStatus: newStatus
    });
  };

  return (
    <Card sx={{ mb: 2 }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <div>
            <Typography variant="h6">: {request.id}</Typography>
            <Typography color="textSecondary">: {request.user} ({request.userPhone})</Typography>
            <Typography color="textSecondary">: {request.project} - {request.term} - {request.engineeringItem}</Typography>
            <Typography color="textSecondary">: {request.deliveryAddress}</Typography>
            <Typography color="textSecondary">: {request.deliveryDate}</Typography>
            <Typography color="textSecondary">: {request.recipientName} ({request.recipientPhone})</Typography>
          </div>
          {!isAllCompleted && (
            <Button 
              variant="contained" 
              color="primary" 
              onClick={handleAllComplete}
              disabled={updatingId === request.id}
              sx={{ minWidth: '100px' }}
            >
              {updatingId === request.id ? <CircularProgress size={24} color="inherit" /> : ''}
            </Button>
          )}
        </Box>
        <TableContainer component={Paper} sx={{ mt: 2 }}>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {request.items.map((item, index) => {
                const uniqueItemId = `${request.id}-${item.subcategory}-${item.thickness}-${item.size}`;
                const isUpdating = updatingId === uniqueItemId;
                return (
                                    <TableRow 
                                      key={index}
                                      sx={{ '&:nth-of-type(odd)': { backgroundColor: (theme) => theme.palette.action.hover } }}
                                    >
                                      <TableCell align="center">{item.category} - {item.subcategory}</TableCell>
                                      <TableCell align="center">{item.thickness}</TableCell>
                                      <TableCell align="center">{item.size}</TableCell>
                                      <TableCell align="center">{item.quantity}</TableCell>
                                      <TableCell align="center">{item.unit}</TableCell>
                                      <TableCell align="center">
                                        <Select
                                          value={item.status}
                                          onChange={(e) => handleItemStatusChange(e, item)}
                                          disabled={isUpdating}
                                          size="small"
                                          sx={{ 
                                            minWidth: 120,
                                            backgroundColor: item.status.trim() === '' ? '#e8f5e9' : 'inherit',
                                            color: item.status.trim() === '' ? '#2e7d32' : 'inherit',
                                          }}
                                        >
                                          <MenuItem value="">
                                            {isUpdating ? <CircularProgress size={20} /> : ''}
                                          </MenuItem>
                                          <MenuItem value=""></MenuItem>
                                        </Select>
                                      </TableCell>
                                    </TableRow>                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </CardContent>
    </Card>
  );
}

export default RequestCard;
