import React from 'react';
import { Card, CardContent, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, CircularProgress, Select, MenuItem } from '@mui/material';

function ReturnCard({ ret, onUpdateStatus, onUpdateItemStatus, updatingId }) {
  const isAllCompleted = ret.items.every(item => item.status.trim() === '');

  const handleAllComplete = () => {
    onUpdateStatus(ret.id, '');
  };

  const handleItemStatusChange = (e, item) => {
    const newStatus = e.target.value;
    onUpdateItemStatus({
      returnId: ret.id,
      itemName: item.name,
      newStatus: newStatus
    });
  };

  return (
    <Card sx={{ mb: 2 }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <div>
            <Typography variant="h6">: {ret.id}</Typography>
            <Typography color="textSecondary">: {ret.user}</Typography>
            <Typography color="textSecondary">: {ret.project}</Typography>
          </div>
          {!isAllCompleted && (
            <Button 
              variant="contained" 
              color="primary" 
              onClick={handleAllComplete}
              disabled={updatingId === `return-${ret.id}`}
              sx={{ minWidth: '100px' }}
            >
              {updatingId === `return-${ret.id}` ? <CircularProgress size={24} color="inherit" /> : ''}
            </Button>
          )}
        </Box>
        <TableContainer component={Paper} sx={{ mt: 2 }}>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {ret.items.map((item, index) => {
                const uniqueItemId = `return-${ret.id}-${item.name}`;
                const isUpdating = updatingId === uniqueItemId;
                return (
                  <TableRow 
                    key={index}
                    sx={{ '&:nth-of-type(odd)': { backgroundColor: (theme) => theme.palette.action.hover } }}
                  >
                    <TableCell align="center">{item.name}</TableCell>
                    <TableCell align="center">{item.quantity}</TableCell>
                    <TableCell align="center">{item.reason}</TableCell>
                    <TableCell align="center">
                      <Select
                        value={item.status}
                        onChange={(e) => handleItemStatusChange(e, item)}
                        disabled={isUpdating}
                        size="small"
                        sx={{ 
                          minWidth: 120,
                          backgroundColor: item.status.trim() === '' ? '#e8f5e9' : 'inherit',
                          color: item.status.trim() === '' ? '#2e7d32' : 'inherit',
                        }}
                      >
                        <MenuItem value="">
                          {isUpdating ? <CircularProgress size={20} /> : ''}
                        </MenuItem>
                        <MenuItem value=""></MenuItem>
                      </Select>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </CardContent>
    </Card>
  );
}

export default ReturnCard;